<template>
    <div class="PAGE ROOT">
            <div v-for="(item, index) in background" 
                :style="{
                    backgroundImage:item.i,
                    backgroundColor:item.c,
                    backgroundPosition:item.pos,
                    backgroundSize:item.size
                }"
                :key="index" 
                :id="'page-background-'+index" 
                class="PAGE-BACKGROUND">
            </div>
        <dynamic-loader-view :data="data.data" />
    </div>
</template>
<script>
import v from '../validators'
var{vNumber} =v;
export default {
    name:'PAGE',
    components:{
        dynamicLoaderView:()=>import("./v.s.loader.component.vue")
    },
    props:{
        data:{
            }
        },
    computed: {
         background(){
             return[{
                    blur:true,
                    c:null,
                    pos:'center',
                }]
        },
    },
    mounted() {
        for(let each in this.background)
            {
                if(this.background[each].letter)
                {
                    this.$el.querySelector('#page-background-'+each).innerText=this.$el.innerText[5]||this.$el.innerText[this.$el.innerText-1]||'䷢';
                }
                if(this.background[each].blur)
                {
                    this.$el.querySelector('#page-background-'+each).style.filter=`blur(${Number(this.background[each].blur||2)}px)`;
                }
                if(this.background[each].attach)
                {
                    this.$el.querySelector('#page-background-'+each).style.backgroundAttachment='fixed';
                }
            }
    },
}
</script>
<style lang="less">
.PAGE.ROOT
{
    display: grid;
    overflow: auto;
    justify-content: stretch;
    align-content: stretch;
    box-sizing: border-box;
    transform: scale(1);
    .PAGE-BACKGROUND
    {
        //should be out of the flow
        position: fixed;
        background-position: center;
        background-size:cover;
        background-repeat: no-repeat;
        z-index:var(--background-zIndex,-100);
        width:100%;
        height:100%;
        display: grid;
        justify-content: center;
        align-content: center;
        font-size:6em;
        text-shadow: 0px 0px 26px #413535;
    }
}
</style>




