<template>
    <span @click="$say('clicked:text')" :class="[ 'TEXT-BASIC', 'ROOT', params.level,params.wrap]" >{{data.text||''}}</span>
</template>
<style lang="less">
.TEXT-BASIC
{
    display: inline-block;
    font-size: inherit;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    &.breakword
    { 
        overflow: auto;
        word-wrap:break-word;
        white-space: normal
    }
    &.wrap
    {
        overflow:unset;
        white-space: normal
    }
    &.h1
    {
        font-size: 2em;
    }
    &.h2
    {
        font-size: 1.5em;
    }
    &.h3
    {
        font-size: 1.17em;
    }
    &.h4
    {
        font-size: 1em;
    }
    &.h5
    {
        font-size: 0.83em;
    }
    &.h6
    {
        font-size: 0.67em;
    }
}
</style>

<script>
/**
 * params={
 *      level:[h1,h2,h3,h4,h5,h6],
 *      wrap:[wrap,breakword]
 * }
 * data.text=String
 */
import v from '../validators.js';
var {vURL,vNumber,vString,vPromise,vObject,vRequired,$OR,$AND}=v;
export default {
    name:"TEXT-BASIC",
    props:['data'],
    fields:{
        data:{
            is:/^text.*/,
            text:vString
        }
    }
}
</script>



