<template>
        <DIV :class="'BOX ROOT'" :style="{
               'grid-template-columns':params.columns||boxStyle.columns,
               'grid-template-rows':params.rows||boxStyle.rows
           }
        ">
            <!-- FIXME: :is must changed to accomodate possobilty of Array/group of components -->
            <dynamic-loader-view class="logo-pos" id="pos1" v-if="!!data.pos1" :data="data.pos1" />
            <dynamic-loader-view class="logo-pos" id="pos2" v-if="!!data.pos2" :data="data.pos2"/>
            <dynamic-loader-view class="logo-pos" id="pos3" v-if="!!data.pos3"  :data="data.pos3"/>
            <dynamic-loader-view class="logo-pos" id="pos4" v-if="!!data.pos4"  :data="data.pos4"/>

            <dynamic-loader-view class="logo-gate" id="header" v-if="!!data.header"  :data="data.header"/>
            <dynamic-loader-view class="logo-gate"  id="footer" v-if="!!data.footer"  :data="data.footer"/>
            <dynamic-loader-view class="logo-gate"  id="lefter" v-if="!!data.lefter"  :data="data.lefter"/>
            <dynamic-loader-view class="logo-gate"  id="righter" v-if="!!data.righter"  :data="data.righter"/>

            <dynamic-loader-view class ="logo-bars" id="head" v-if="!!data.head"  :data="data.head"/>
            <dynamic-loader-view class ="logo-bars" id="foot" v-if="!!data.foot"  :data="data.foot"/>
            <dynamic-loader-view class ="logo-bars" id="left" v-if="!!data.left"  :data="data.left"/>
            <dynamic-loader-view class ="logo-bars" id="right" v-if="!!data.right"  :data="data.right"/>
            <dynamic-loader-view class ="logo-bars" id="center" v-if="!!data.center"  :data="data.center"/>
            <dynamic-loader-view class ="logo-bars" id="cover" v-if="!!data.cover"  :data="data.cover"/>

            
        <!-- though  the following components are expected to be pic and text-content type,
            flexibilty of using other compoents is intetionally given
         -->
         <span id="content-box">
            <dynamic-loader-view class="BOX-CONTENT" v-if="!!data.content"  :data="data.content"/>
        </span>
        </DIV>
</template> 
<style lang="less">
.BOX.ROOT
    {
        
        display: inline-grid;
        position: relative;//to create stacking context;
        overflow: auto;
        grid-template-areas: "pos1  header  pos2"
                             "lefter  contentBox  righter"
                             "pos4   footer  pos3";
        justify-items: stretch;
        align-items: stretch;
        width:auto;
        height: auto;
        & > *
        {
            //so that items becomes collapsible with grid
            min-width: 0px;
            min-height: 0px;
        }
        #pos1 
        {
            grid-area:pos1;
        }
        #pos2
        {
            grid-area: pos2;
        }
        #pos3
        {
            grid-area: pos3;
        }
        #header
        {
            grid-area:header;
        }
        #footer
        {
            grid-area:footer;
        }
        #lefter
        {
            grid-area:lefter;
        }
        #righter
        {
            grid-area:righter;
        }
        #head
        {
            grid-column:1 / span 3;
            grid-row:1 / span 1;
        }
        #foot
        {
            grid-column:1 / span 3;
            grid-row:3 /span 1;
        }
        #left
        {
            grid-column: 1 / span 1;
            grid-row:1/ span 3;
        }
        #right
        {
            grid-column:3 /span 3;
            grid-row:3 / span 1;
        }
        #center
        {
            
                grid-area:contentBox;
                overflow: auto;
                display: inline-grid;
                justify-content: center;
                 align-content: center;
                 resize: both;
                 z-index: 3;
        }
        #cover
        {
            
                grid-column: 1 /span 3;
                grid-row: 1 /span 3;
                overflow: auto;
                display: inline-grid;
                justify-content: stretch;
                 align-content: stretch;
                 z-index: 1;
        }
        #content-box
        {
            
                grid-column: 1 /span 3;
                grid-row: 2 /span 2;
                overflow: auto;
                display: inline-grid;
                justify-content: stretch;
                 align-content: stretch;
                // zoom: 0.7;
                z-index:1;
        }
        .logo-pos
        {
            z-index: 4;
        }
        .logo-gate
        {
            z-index: 2;
        }
        .logo-bars
        {
            z-index: 3;
        }
        // &.BOX.ROOT{
        //     --box-vSides-left:unset;
        //     --box-hSides-top:unset; 
        //     --box-hSides-bottom:unset; 
        //     --box-hSides-right:unset; 
        //     --box-hSides:unset; 
        //     --box-vSides:unset;

        //     grid-template-columns: var(--box-vSides-left,var(--box-vSides,var(--logo-length,2em))) auto var(--box-vSides-right,var(--box-vSides,var(--logo-length,2em)));
        //     grid-template-rows: var(--box-hSides-top,var(--box-hSides,var(--logo-length,2em))) auto var(--box-hSides-bottom,var(--box-hSides,var(--logo-length,2em)));
        //  }
    }
</style>
<script>
import v from "../validators"
var {$OR, vString,vObject,vPromise,vURL}=v;
export default {
    name:'BOX',
    components:{
        dynamicLoaderView:()=>import("./v.s.loader.component.vue")
    },
    props:{
        data:Object
    },
    computed: {
        boxStyle:function()
            {
                return this.data.boxStyle ||{
                columns:'2em auto 2em',
                rows:'2em auto 2em'}
            }
    },
}
</script>



