<template>
    <div class="LIST ROOT">
        <component-loader class="LIST-ITEM" v-for="(item, index) in data.items" :key="index" :data="item" />
    </div>
</template>
<script>
import v from '../validators.js';
import componentLoader from "./v.s.loader.component.vue";
var {$MAP,vArray}=v
export default {
    name:'LIST-VIEW',
    components:{
        componentLoader
    },
    props:['data'],
}
</script>
<style lang="less">

</style>



